@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --orange:#ff7800;
    --black:#130f40;
    --outline-hover:.2rem solid var(--black) ;
    --box-shadow:0 .5rem 1rem rgba(0,0,0,.1);
    --light-color:#666;
    --border:.2rem solid rgba(0,0,0,.1);
    --outline:.1rem solid rgba(0,0,0,.1)
}

html{
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    scroll-padding-top: 7rem;
    scrollbar-color: orange;
}

.containner{
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    box-shadow: var(--box-shadow);
    background-color: #fff;
    width: 100%;
}

.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:1rem 5%;
    top:0; left: 0; right: 0;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    box-shadow: var(--box-shadow);
    background-color: #fff;
    position: fixed;
    z-index: 1000;

    width: 100%;
    margin: 0 auto;
    max-width: 1370px;
}

.navbar .logo-container .logo{
    margin-right: 1.5rem;
    color: orangered;
    cursor: pointer;
}

.navbar .huo{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5px;
}

.navbar .huo .cart-wrap{
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    
}

.navbar .huo .cart-wrap .cart-text{
font-size: 15px;
margin-left: 10px;
}


.navbar .huo .cart-wrap .badge{
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar .huo .cart-wrap .badge .cartColor{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    margin: 0;
    padding: 0;
}

/************ MEDIA QUERY ***********/

@media (max-width: 480px) {
    .navbar .huo .cart-wrap .cart-text{
        display: none;
    }
}




/*############################ SEARCH BAR START #####################*/

.navbar form{
    display: flex;
    justify-content: center;
    width: 40rem;
    height: 3.5rem;
}

.navbar form input{
    width: 100%;
    outline: none;
    border: 2px solid lightgray;
}

.navbar form .search-icon{
    width: 3.3rem;
    height: 100%;
    background-color: orange;
    color: black;
    
}

.navbar form .search-icon:hover{
    /* box-shadow: .1rem .1rem .3rem .1rem orangered; */
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
    cursor: pointer;
}

/*###################* SEARCH BAR END ##################################*/




/*###################* NAV LINKS START ##################################*/

.menu 
{
    position: relative;
    font-size: 15px;
    cursor: pointer;
}

.menu > .li, .menu > a{
    display: inline-block;
}

.menu a{
    color: black;
    display: block;
    margin: 5px 1.5rem;
    font-size: 15px;
}


.menu a:hover{
    color:orangered;
}

.subMenu{
    position: absolute;
    background-color: white;
    left: 0;
    display: none;
}

.subMenu .li{
    padding: 10px;
}

.porel {
    display: flex;
    justify-content: space-between;
}

/* .porel{
    display: flex;
    flex-direction: column;
} */

.subMenu2{
    position: absolute;
    left: 100%;
    width: 140px;
    top: 0;
    background-color: white;
    display: none;
}

.subMenu22{
    position: absolute;
    left: 100%;
    width: 140px;
    top: 50px;
    background-color: white;
    display: none;
    /* border: 1px solid black; */
}


.menu .li:hover > .subMenu{
    display: block;
}

.menu .li:hover > .subMenu2{
    display: block;
}

.menu .li:hover > .subMenu22{
    display: block;
}


/************ MEDIA QUERY ***********/

@media (max-width: 840px) {
    .menu{
        display: none;
    }
}


/*###################* NAV LINKS END ####################################*/




