.nav-menu{
    background-color:rgb(255, 255, 255);
    height: calc(90vh - 9rem);
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    z-index: 1001;
    left: -100%;
    transition: 850ms;
    overflow-y: scroll;
}

.nav-menu.active{
    left: 0;
    transition: 350ms;

}

.nav-text{
font-size: 1.5rem;
}

